@use 'sass:math'; @import '/Users/nickmajor/projects/rw/care-ops-frontend/src/scss/provider-variables.scss';
.fill-window {
  height: 100%;
  left: 0;
  overflow: hidden;

  // allows for below interactions on non-IE browsers
  pointer-events: none;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;

  > * {
    pointer-events: all;
  }
}

.fill-window--dark {
  @extend .fill-window;

  background: rgb(0 0 0 / 50%);
  pointer-events: all;
}
